  created() {},
  mounted() {
    this.formData = JSON.parse(JSON.stringify(this.allPageInfo));
    const { EC_B_OVERVIEW } = this.formData;
    if (EC_B_OVERVIEW) {
      EC_B_OVERVIEW.INTAKING_TYPE_CODE = EC_B_OVERVIEW.INTAKING_TYPE_CODE.split(
