    }
  },
  methods: {
    submitForm() {
      this.$refs["elForm"].validate((valid) => {
        if (!valid) return;
        // TODO 
        this.loading = true;
        const params = JSON.parse(JSON.stringify(this.formData));
        params.EC_B_OVERVIEW.INTAKING_TYPE_CODE = this.formData.EC_B_OVERVIEW.INTAKING_TYPE_CODE.join(
