          ","
        );
        axios
          .post(`http://58.42.237.177:8975/pdcApi/fillIn/saveInfo`, params)
          .then((res) => {
            console.log(res, "res");
            if (res.data.success) {
              this.$message.success("");
            } else {
              this.$message.error(res.data.message);
            }
          })
          .finally(() => {
            this.loading = false;
          });
      });
    },
    resetForm() {
      this.$refs["elForm"].resetFields();
    },
  },
