        IND_CLASS_CODE: "",
        POSTAL_CODE: "",
        REG_ADDR: "",
        OP_LOC_DIV_CODE: "",
        OP_LOCATION: "",
        CON_PER: "",
        CELL_PHONE: "",
        PRO_NAT_CODE: "",
        PRO_NAME: "",
      },
      rules: {
        WAT_RIG_OWNER: [
