          },
        ],
        OP_LOCATION: [
          {
            required: true,
            message: ",!",
            trigger: "blur",
          },
        ],
        CON_PER: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        CELL_PHONE: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
      },
    };
  },
  computed: {
    ...mapGetters({
      allPageInfo: "waterForm/allPageInfo",
      allDictData: "waterForm/allDictData",
      areaList: "waterForm/areaList",
      industryList: "waterForm/industryList",
    }),
  },
  watch: {},
  created() {},
  mounted() {
    this.formData = JSON.parse(JSON.stringify(this.allPageInfo?.EC_B_WATER_RIG_OWNER));
  },
  methods: {
    onAddTableData() {
      if (!this.formData.PART_OWNERS) {
        this.$set(this.formData,"PART_OWNERS", [])
      }
      this.formData.PART_OWNERS.push({
        NAME: "",
        SHARE: "",
        CODE: "",
        CODE_TYPE: "",
      });
    },
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },
    submitForm() {
      console.log(this.formData, "formData");
      this.$refs["elForm"].validate((valid) => {
        if (!valid) return;
        // TODO 
        this.loading = true;
        this.formData.MATTER_ID = this.allPageInfo.MATTER_ID;
        axios
          .post(
            `http://58.42.237.177:8975/pdcApi/fillIn/saveEcBWaterRigOwner`,
            this.formData
          )
          .then(res => {
            if(res.data.success) {
              this.$message.success("");
            } else {
              this.$message.error(res.data.message);
            }
          })
          .finally(() => {
            this.loading = false;
          });
      });
    },
    resetForm() {
      this.$refs["elForm"].resetFields();
    },
  },
};
</script>
